import React, { useEffect, useState } from 'react';
import { socket } from '../../services/general.services';
import { Icon, Form, Button } from 'semantic-ui-react';
import { RouteComponentProps } from 'react-router-dom';

import './Message.scss';

const Message = ( props ) => {
    const { match: { params: { username, room } } } = props;
    const [users, setUsers] = useState( [] );
    const [rooms, setRooms] = useState( '' );
    const [messages, setMessages] = useState( [] );

    useEffect( () => {
        socket.emit( 'joinroom', { username, room } );
        socket.on( 'roomusers', ( { room, users } ) => {
            setUsers( users );
            setRooms( room );
        } );

        socket.on( 'message', message => {
            setMessages( prev => [...prev, message] );
        } );
    }, [] );

    const onSubmitHandler = ( e ) => {
        e.preventDefault();
        const message = e.target.elements.message.value;

        socket.emit( 'chatMessage', message );

        e.target.elements.message.value = '';
    };

    const leaveRoom = () => {
        socket.emit( 'disconnect', socket.id );
        socket.close();
        props.history.replace( '/' );
    };
    return (
        <>
            <div className='container'>
                <div className='side-bar'>
                    <header className='side-bar-header'>
                        <h1><Icon name='smile' color='white' />Chatter Box</h1>
                        <Button onClick={ leaveRoom }>Leave Room</Button>
                    </header>
                    <h3><Icon name='comments' />Room Name</h3>
                    <h2>{ rooms }</h2>
                    <h3><Icon name='users' />Users</h3>
                    <ul>
                        { users.map( ( user ) => <li key={ user.id }>{ user.userName }</li> ) }
                    </ul>
                </div>
                <div className='messages'>
                    <div className='chat-messages'>
                        { messages.map( msg => {
                            return (
                                <div className='message'>
                                    <p className='meta'>{ msg.username }<span>{ msg.time }</span></p>
                                    <p className='text'>{ msg.text }</p>
                                </div>
                            );
                        } ) }
                    </div>
                    <div className='footer'>
                        <Form onSubmit={ ( e ) => onSubmitHandler( e ) }>
                            <div className='form-elements'>
                                <Form.Input name='message' required style={ { width: '100%' } } placeholder='Enter a message' autocomplete='off' />
                                <Button type='submit'>Send</Button>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Message;
