import React, { useState } from 'react';

import './Login.scss';
import { Form } from 'semantic-ui-react';
import { RouteComponentProps } from 'react-router-dom';

const Login = ( props ) => {
    const options = [
        {
            key: 'javascript',
            value: 'javascript',
            text: 'Javascript'
        },
        {
            key: 'f1',
            value: 'f1',
            text: 'Formula 1'
        }
    ];

    const [room, setRoom] = useState( '' );
    const onChangeHandler = ( e, data ) => {
        setRoom( data.value );
    };
    const onSubmitHandler = ( e ) => {
        const username = e.target.elements.username.value;
        if ( room === undefined || room === '' ) {
            alert( 'Please Select a room' );
        } else {
            props.history.replace( `/message/${ username }/${ room }` );
        }
    };
    return (
        <>
            <div className='background'>
                <div className='login-form'>
                    <p>Login</p>
                    <Form onSubmit={ ( e ) => onSubmitHandler( e ) }>
                        <Form.Input className='form-input' required width='10' name='username' label='User Name' />
                        <Form.Select className='form-input' width='10' onChange={ ( e, data ) => onChangeHandler( e, data ) } name='room' options={ options } />
                        <Form.Button className='form-input' type='submit'>Login</Form.Button>
                    </Form>
                </div>
            </div>
        </>
    );
};

export default Login;
